import React, {useState} from 'react';
import './Nav.css'


function Nav({handleToggle, toggle}) {
  const [stickyNav, setStickyNav] = useState(false)
  const scrolled = () =>{
    if(window.scrollY > 4){
      setStickyNav(true)
    }
    else{
      setStickyNav(false)
    }
  }
  
  window.onscroll =  scrolled;

  return <div className={stickyNav ? 'navbar-container active' : 'navbar-container'}>
    <h6 className='logo'>SVG LOGO</h6>
    <div className={toggle ? 'nav-display open' : 'nav-display close'}>
      <ul className='nav-items'>
        <li>Home</li>
        <li>Inventory</li>
        <li>Finance</li>
        <li>Contact</li>
      </ul>
      <div className="user-login">
        <h4>Sign Up</h4>
        <h4 className='seperator-bar'> &nbsp; | &nbsp; </h4>
        <h4>Sign In</h4>
      </div>
    </div>
    <div className="menu-toggle"
      onClick={handleToggle}>
      {toggle ? <i className="fas fa-times"></i> : <i className="fas fa-bars"></i>}
    </div>
  </div>;
}

export default Nav;
